html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  min-width: 320px;
  position: relative;
  width: 100%;
  color: #000; }

body {
  font-family: "PT Sans";
  font-size: 14px;
  line-height: 1; }
  body.lock {
    overflow: hidden; }
    @media (max-width: 767.98px) {
      body.lock {
        width: 100%;
        position: fixed;
        overflow: hidden; } }

input, button, textarea {
  font-family: "PT Sans"; }

input {
  display: none; }

button {
  cursor: pointer;
  padding: 0;
  border: 0; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit; }

/*WRAPPER*/
.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden; }

/*MAIN GRID*/
.container {
  max-width: 880px;
  margin: 0 auto;
  width: 100%; }
  @media (max-width: 892px) {
    .container {
      max-width: 750px; } }
  @media (max-width: 992.98px) {
    .container {
      max-width: none;
      padding: 0 10px; } }

header {
  padding: 29px 0 33px 0;
  background-color: #fff; }
  header .header-top {
    display: flex;
    font-size: 0;
    justify-content: space-between; }
    header .header-top-lang {
