      font-size: 0; }
      header .header-top-lang__item {
        display: block;
        line-height: 19px;
        color: #000;
        font-size: 14px;
        text-transform: uppercase;
        font-family: "Bebas", sans-serif;
        letter-spacing: 1px;
        padding: 0 4px;
        margin: 0 10px 0 0; }
        header .header-top-lang__item.active {
          background-color: #000;
          color: #fff; }
    header .header-top__link {
      font-family: "AkzidenzGroteskBQCondensedBold", sans-serif;
